.what-is-townhouse {
	padding: 70px 0 90px 0px;
	@include r(1099) {
		padding: 50px 0 60px 0px; }
	@include r(1200) {
		overflow: hidden; }
	.container {
		&:before {
			content: '';
			position: absolute;
			top: 35%;
			left: 0;
			@include s(slider-line);
			z-index: 0;
			@include r(1099) {
				left: 50%;
				transform: translateX(-50%); }
			@include r(767) {
				display: none; } } }
	.desc-wrapper {
		text-align: justify; }
	.col, .col-last {
		color: $simple-text-color;
		font: $simple-text-style;
		vertical-align: top;
		@include r(1099) {
			font: $simple-text-style-tablet-mobile; } } }
.left-slider-col {
	margin-right: 63px; }
.what-is-townhouse__h2 {
	margin-bottom: 30px;
	margin-top: 5px;
	@include r(1099) {
		margin-bottom: 20px; }
	@include r(767) {
		text-align: center; } }
.what-is-townhouse__h2-mod {
	position: absolute;
	top: -110px;
	@include r(1099) {
		top: -90px; }
	@include r(767) {
		text-align: center;
		position: relative;
		top: 0;
		margin-top: 20px;
		br {
			display: none; } } }
.townhouse__galery {
	position: absolute;
	right: -15px;
	top: 44%;
	color: #375272;
	font: 400 18px;
	font-family: $font-family, sans-serif;
	text-transform: uppercase;
	transform: rotate(-10deg);
	transition: all .3s;
	@include r(1099) {
		right: 100px;
		top: 59%; }
	@include r(767) {
		display: none; }
	&:hover {
		text-decoration: underline; } }
