.container {
	position: relative;
	@include rmin(320) {
		width: 100%;
		padding:  0 15px; }
	@include rmin(768) {
		width: 750px;
		margin: 0 auto;
		padding:  0 20px; }
	@include rmin(1100) {
		width: 1000px;
		padding:  0 40px; } }
.col, .col-last {
	display: inline-block;
	vertical-align: top;
	position: relative;
	width: 280px;
	margin-right: 35px;
	text-align: left;
	@include r(1099) {
		width: 200px; }
	@include r(767) {
		width: 100%;
		margin-bottom: 20px; } }
.col {
	@include r(1099) {
		margin-right: 50px; }
	@include r(767) {
		margin-right: 0; } }
.col-last {
	margin-right: 0; }
