$font-family: "Open Sans";
$simple-text-color: #222;
$simple-text-style: 400 16px/1.8em "Open Sans", sans-serif;
$simple-text-style-tablet-mobile: 400 14px/1.7em "Open Sans", sans-serif;
$header-menu-item-color: #ac975e;
$contact-phone-color: #aac8ea;
$tab-cols: (1: #b84848, 2: #b8b748, 3: #6db848, 4: #48b7b8, 5: #4862b8, 6: #b848aa);


$contacts-custom-color: #aac8ea;
* {
	box-sizing: border-box; }

.h2 {
	color: #375272;
	font: 600 30px/1.2em $font-family, sans-serif;
	text-transform: uppercase;
	@include r(1100) {
		font: 600 22px/1.3em $font-family, sans-serif; }
	@include r(768) {
		font: 600 16px/1.25em $font-family, sans-serif; } }
.input-default, .textarea-default {
		font: 400 16px $font-family, sans-serif;
		font-style: italic;
		padding: 10px 10px;
		width: 100%;
		box-shadow: inset 2.1px 2.1px 3px rgba(25, 24, 20, 0.24);
		@include tr(all .3s);
		&:hover {
			box-shadow: inset 0 0 3px rgba(25, 24, 20, 0.24); } }
.button-default {
	width: 100%;
	background: #ff151f;
	padding: 10px;
	color: #fff;
	font: 600 16px $font-family, sans-serif;
	text-transform: uppercase;
	@include tr(all .3s);
	&:hover {
		color: #ff151f;
		background: #fff;
		box-shadow: inset 0 0 3px rgba(25, 24, 20, 0.24); }
	&:active {
		box-shadow:  0 0 3px rgba(25, 24, 20, 0.24); } }
.close-default {
	display: block;
	text-align: center;
	color: #fff;
	font: 400 16px/1.6em $font-family, sans-serif;
	width: 26px;
	height: 26px;
	background: #8a2222;
	border-radius: 50%;
	cursor: pointer;
	opacity: 1;
	right: 15px;
	top: 15px;
	@include r(1100) {
		width: 40px;
		height: 40px;
		font-size: 24px;
		line-height: 40px; }
	@include tr(all .3s);
	&:before {
		content: 'X';
		position: absolute;
		@include vcenter; }
	&:hover {
		box-shadow:  0 0 3px rgba(25, 24, 20, 0.24);
		color: #8a2222;
		background: #fff; } }
