.about-builder__h2 {
	color: #21afa4;
	text-shadow: 0px 0px 2px #21afa4;
	text-align: center;
	position: relative;
	display: inline-block;
	font-size: 31px;
	margin-bottom: 25px;
	@include r(767) {
		font-size: 16px; }
	&:before, &:after {
		content: '';
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		@include s(about-builder);
		@include r(767) {
			display: none; } }
	&:before {
		left: -127px; }
	&:after {
		right: -127px;
		@include s(about-builder2); }
	&+p {
		color: #ef3122;
		text-transform: uppercase;
		font: 700 20px $font-family, sans-serif;
		margin-bottom: 56px;
		@include r(1099) {
			font-size: 18px; }
		@include r(767) {
			font-size: 14px;
			margin-bottom: 10px; } } }
.about-builder {
	padding-top: 50px;
	padding-bottom: 75px;
	text-align: center;
	background: #f5f5f5; }
.desc {
	text-align: left; }
.desc__second-block {
	text-align: center;
	color: #222;
	font: 400 16px/1.87em $font-family, sans-serif;
	text-transform: none;
	@include r(767) {
		margin-top: 20px; }
	p.second-block__p {
		margin: 25px 0; }
	span {
		color: #222; }
	a {
		color: #ff151f;
		line-height: 1.5em;
		@include r(767) {
			display: block; } } }
.desc__third-block {
	text-align: right;
	p:last-of-type {
		font-size: 16px;
		text-transform: none;
		line-height: 1.25em;
		@include r(767) {
			font-size: 14px; } } }
.desc__first-block, .desc__third-block {
	padding-top: 10px;
	color: #222;
	font: 400 24px/1.25em $font-family, sans-serif;
	text-transform: uppercase;
	@include r(1099) {
		font: 400 18px/1.3em $font-family, sans-serif; }
	@include r(767) {
		font: $simple-text-style-tablet-mobile;
		text-transform: none;
		margin-bottom: 0;
		text-align: center; }
	p {
		margin-bottom: 45px;
		@include r(1099) {
			margin-bottom: 20px; }
		@include r(767) {
			margin-bottom: 10px;
			br {
				display: none; } }
		&:last-of-type {
			margin-bottom: 0; } }
	span {
		color: #4faea2;
		font-size: 36px;
		font-weight: 700;
		@include r(1099) {
			font-size: 28px; }
		@include r(767) {
			font-size: 20px; }
		span {
			font-size: 24px;
			@include r(1099) {
				font-size: 18px; }
			@include r(767) {
				font-size: 16px; }
			text-transform: none; }
		&.gold-partner {
			font-size: 24px;
			text-transform: uppercase;
			@include r(767) {
				font-size: 20px; } } } }
