.adv-popup-block {
	max-width: 630px;
	width: 100%;
	border-radius: 20px;
	background: #fff;
	padding: 30px 45px;
	position: relative;
	margin: 0 auto;
	@include clr;
	.close-default {
		opacity: 1;
		color: #fff;
		position: absolute;
		right: 15px;
		top: 15px;
		&:hover {
			color: #8a2222; } } }
.adv-popup-block__img {
	width: 200px;
	height: 200px;
	border-radius: 50%;
	@include cover;
	border: 3px solid #375272;
	float: left;
	margin-right: 37px;
	@include r(767) {
		display: none; } }
.adv-popup-block__title {
	margin-top: 30px;
	margin-bottom: 14px;
	vertical-align: top;
	color: #375272;
	font: 600 20px/1.5em $font-family, sans-serif;
	@include r(767) {
		margin-top: 0; } }
.adv-popup-block__description {
	display: block;
	color: #222;
	font: 400 16px/1.5em $font-family, sans-serif; }
.mfp-fade {
  &.mfp-bg {
    opacity: 0;
    -webkit-transition: all 0.15s ease-out;
    -moz-transition: all 0.15s ease-out;
    transition: all 0.15s ease-out;
    &.mfp-ready {
      opacity: 0.8; }
    &.mfp-removing {
      opacity: 0; } }
  &.mfp-wrap {
    .mfp-content {
      opacity: 0;
      -webkit-transition: all 0.15s ease-out;
      -moz-transition: all 0.15s ease-out;
      transition: all 0.15s ease-out; }
    &.mfp-ready .mfp-content {
      opacity: 1; }
    &.mfp-removing .mfp-content {
      opacity: 0; } } }
