.contacts__h2 {
	color: $contacts-custom-color;
	margin-bottom: 40px;
	@include r(767) {
		margin-bottom: 20px; } }
.contacts {
	padding-bottom: 175px;
	padding-top: 92px;
	background: url("../img/advantages_bg.png")  no-repeat;
	background-size: cover;
	color: #fff;
	font: $simple-text-style;
	line-height: 1.5em;
	@include r(1099) {
		padding-top: 60px;
		font: $simple-text-style-tablet-mobile;
		.col:first-of-type {
			margin-right: 20px; } }
	@include r(767) {
		text-align: center;
		padding-top: 40px; }
	.col {
		@include r(767) {
			text-align: center; }
		&:first-of-type {
			@include r(767) {
				margin-right: 0px; } } }

	a {
		color: #fff; }
	strong, .custom-color {
		font-weight: inherit;
		color: $contacts-custom-color;
		a {
			color: inherit; } }
	p {
		margin-bottom: 30px;
		@include r(767) {
			margin-bottom: 10px; } } }
.callback-questions {
	text-align: center;
	width: 320px;
	padding: 27px 35px 25px;
	background: #aac8ea;
	position: absolute;
	right: 0px;
	z-index: 11;
	top: 0;
	@include r(1099) {
		width: 280px; }
	@include r(767) {
		position: relative;
		margin: 0 auto; }
	input {
		margin-bottom: 10px; }
	textarea {
		resize: none;
		height: 150px;
		margin-bottom: 20px; } }
.callback-questions__header {
	color: #375272;
	font: 600 30px/1em $font-family, sans-serif;
	text-transform: uppercase;
	margin-bottom: 20px;
	@include r(1099) {
		font-size: 24px; } }
