.callback-block {
	background: #aac8ea;
	width: 320px;
	position: absolute;
	right: 0;
	top: 0;
	padding: 25px 35px 25px;
	z-index: 22;
	display: none;
	@include r(1099) {
		width: 300px;
		padding: 25px; }
	@include r(767) {
		width: 100%; }
	.img-block {
		width: 102px;
		height: 102px;
		background: #fff;
		border-radius: 50%;
		border: 1px solid #375272;
		position: absolute;
		@include vcenter;
		top: 130px;
		@include r(1099) {
			width: 80px;
			height: 80px; }
		&:before {
			content: '';
			position: absolute;
			@include s(phone-blue);
			@include vcenter; } }
	input {
		margin-bottom: 15px; }
	button {
		margin-top: 30px; } }
.callback-block__phone {
	color: $contact-phone-color;
	font: 700 24px $font-family, sans-serif;
	text-align: center;
	display: block;
	float: none;
	text-transform: uppercase;
	vertical-align: top;
	color: #375272;
	margin-bottom: 150px;
	@include r(1099) {
		font-size: 20px; }
	@include r(767) {
		margin-bottom: 10px; } }
.callback-block__name {
	color: #375272;
	font: 400 14px/1.3em $font-family, sans-serif;
	text-transform: uppercase;
	position: absolute;
	right: 35px;
	text-align: right;
	width: 100px;
	top: 76px;
	@include r(767) {
		position: relative;
		text-align: center;
		width: 100%;
		top: 0;
		right: 0;
		margin-bottom: 120px; } }
.callback-block__close {
	position: absolute;
	right: 35px;
	top: 150px;
	@include r(1099) {
		top: 135px; } }
