.first-scrin {
	padding-top: 84px;
	padding-bottom: 50px;
	text-align: center;
	position: relative;
	@include r(1099) {
		padding-top: 50px; }
	&:before {
		content: '';
		@include coverdiv;
		background: url("../img/first-scrin_bg.png") no-repeat;
		background-size: cover;
		background-position: 50% 0;
		z-index: -1; } }
h1 {
	color: #134423;
	text-transform: uppercase;
	font: 600 65px $font-family, sans-serif;
	margin-bottom: 25px;
	@include r(1099) {
		font-size: 35px; }
	@include r(767) {
		font-size: 20px; }
	span {
		font-size: 30px;
		display: block;
		@include r(1099) {
			font-size: 18px; }
		@include r(767) {
			font-size: 16px; } } }
.fs-description {
	max-width: 602px;
	border: 2px solid #aac8ea;
	border-radius: 15px;
	background: #fff;
	margin: 0 auto;
	padding: 50px 145px;
	position: relative;
	color: #24251e;
	font: 400 25px $font-family, sans-serif;
	text-align: left;
	@include r(1099) {
		font: 400 18px $font-family, sans-serif;
		max-width: 500px;
		padding: 50px 135px; }
	@include r(767) {
		padding: 20px 30px; }
	p {
		margin: 0 auto;
		@include r(767) {
			width: 100%;
			text-align: center; } }
	&:before {
		content: '';
		position: absolute;
		top: 50%;
		transform: translateY(-50%); }
	&:before {
		@include s(desc-image);
		left: 35px;
		@include r(1099) {
			left: 20px; }
		@include r(767) {
			display: none; } }
	span {
		color: #b72e22;
		font-weight: 700; } }
.fs-description__key-block {
	position: absolute;
	right: 30px;
	top: 58px;
	width: 100px;
	height: 100px;
	color: #000;
	font: 400 12px $font-family, sans-serif;
	@include r(1099) {
		top: 53px; }
	@include r(767) {
		display: none; }
	&:before {
		content: '';
		position: absolute;
		@include s(desc-image-2);
		top: 45px;
		left: 50%;
		transform: translateX(-50%);
		@include r(1099) {
			top: 37px; } }
	&>span {
		color: #b72e22; } }
.other-progect-link {
	display: block;
	position: absolute;
	width: 125px;
	height: 100px;
	left: 0;
	top: 300px;
	background: #fff;
	border-radius: 0 45% 45% 0;
	z-index: 2;
	@include r(1099) {
		top: 100px; }
	@include r(767) {
		display: none; }
	&:before {
		content: '';
		position: absolute;
		left: 45px;
		top: 50%;
		transform: translateY(-50%);
		@include s(another-link-img); } }
